import React, { useState, InputHTMLAttributes, memo, ChangeEvent, MouseEvent } from 'react';
import s from './CustomInput.module.scss';
import Upload from 'images/Upload.svg';
import IconPhoto from 'images/IconPhoto.svg';
import { CustomButton } from 'components/CustomButton/CustomButton';

type HTMLInputProps = Omit<InputHTMLAttributes<HTMLInputElement>, 'value' | 'onChange' | 'readOnly'>

interface CustomInputProps extends HTMLInputProps {
  htmlFor: string
  className?: string;
  value?: string;
  onChange?: (value: any) => void
  //placeholder?: string;
  autofocus?: boolean;
  readOnly?: boolean;
  disabled?: boolean;
  background?: 'white' | 'grey';
  accept?: string;
  variant?: 'field' | 'box';
  label?: string;
}

const CustomSelectFile = memo((props: CustomInputProps) => {
  const {
    htmlFor,
    className,
    value,
    onChange,
    //placeholder,
    autofocus,
    readOnly,
    disabled = false,
    background = 'white',
    variant = 'field',
    label = 'Select file:',
    ...otherProps
  } = props;

  const [isFocused, setIsFocused] = useState<boolean>(false);
  const [imageName, setImageName] = useState<string | undefined>(undefined);

  const onChangeHandler = (e: ChangeEvent<HTMLInputElement>) => {
    console.log('111');
    if (!e.target.files) return;
    console.log('222');
    const file = e.target.files[0];
    console.log('333');
    if (file) {
      console.log('555');
      const reader = new FileReader();
      reader.onload = () => {
        onChange?.(reader.result);
        setImageName(file.name);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleFocus = () => {
    setIsFocused(true);
  };

  const handleBlur = () => {
    setIsFocused(false);
  };

  const handleImage = (event: MouseEvent<HTMLImageElement>) => {
    event.preventDefault();
  };

  const handleDelete = (event: MouseEvent<HTMLButtonElement>) => {
    event.preventDefault();
    setImageName(undefined);
    onChange?.(null);
  };

  const isBox = variant === 'box';
  const icon = isBox
    ? <IconPhoto className={s.boxIcon}/>
    : <Upload className={s.fieldIcon}/>;

  const firstItem = isBox
    ? (value
      ? <img src={value} className={s.boxImage} onClick={handleImage}/>
      : icon)
    : label;

  const secondItem = isBox
    ? (value
      ? <div className={s.changeBlock}>
        <CustomButton
          text="Delete"
          variant='outlineWhite'
          size='m'
          width='content'
          onClick={handleDelete}
        />
        <CustomButton
          text={<label htmlFor={htmlFor}>Change</label>}
          variant='action'
          size='m'
          width='content'
        />
      </div>
      : label
    )
    : (imageName
      ? <span className={s.fileName}>{imageName}</span>
      : icon
    );

  return (
    <div className={`${s.inputContainer} ${className}`}>
      <label
        htmlFor={htmlFor}
        className={`${s[variant]} ${s[background]} ${value && s.boxImageContainer}`}
      >
        {firstItem}
        {secondItem}
        <input
          type='file'
          onChange={onChangeHandler}
          disabled={disabled}
          onFocus={handleFocus}
          onBlur={handleBlur}
          id={htmlFor}
          {...otherProps}
        />
      </label>
    </div>
  );
});

export { CustomSelectFile };
