import s from './styleProvider';
import bs from '../banner/styleProvider';
import hs from '../../miniSiteCommon/header/styleProvider';
import React, { memo, useContext, useState } from 'react';
import { CustomButton } from 'components/CustomButton/CustomButton';
import { MiniSiteContext } from 'templatesReact/miniSiteCommon/Context';
import { CustomSelectFile } from 'components/CustomInput/CustomSelectFile';

type PropsT = {
  onClose?: () => void
}

const BannerModalContent = memo((props: PropsT) => {
  const {
    onClose,
  } = props;

  const { data, editImages } = useContext(MiniSiteContext);

  const [currentImage, setCurrentImage] = useState(data.imageUrl);
  const [currentLogo, setCurrentLogo] = useState(data.logoUrl);

  const submit = () => {
    editImages(
      currentImage,
      currentLogo,
    );
    onClose!();
  };

  return (
    <div className={s.body}>
      <div className={hs.frame}>
        <div className={hs.imageContainer}>
          <img className={hs.image} src={currentLogo} alt='logo'/>
        </div>
        <h1 className={hs.title}>{data.name}</h1>
      </div>

      <CustomSelectFile
        htmlFor="updateLogo"
        value={currentLogo}
        onChange={setCurrentLogo}
        label='Update logo:'
      />

      <CustomSelectFile
        htmlFor="updateImage"
        value={currentImage}
        onChange={setCurrentImage}
        variant='box'
        label='Update banner'
        className={bs.body}
      />

      <div className={s.buttonContainer}>
        <CustomButton
          text="Cancel"
          variant='outline'
          size='l'
          width='standard'
          onClick={onClose}
        />
        <CustomButton
          text="Save changes"
          variant='action'
          size='l'
          width='standard'
          onClick={submit}
        />
      </div>
    </div>
  );
});

export { BannerModalContent };
