import React, { memo } from 'react';
import { Modal, ModalPublicProps } from 'components/modal/Modal';
import { useToggle } from 'hooks/useToggle';
import { BannerModalContent } from './BannerModalContent';


const BannerModal = memo((props: ModalPublicProps) => {
  const {
    className,
    isOpen,
    onClose,
    onOverlay,
    onEscape,
  } = props;

  const [triggerClosing, onTriggerClosing] = useToggle(false);

  return (
    <Modal
      className={className}
      title={'Change banner image'}
      widthSize='xl'
      isOpen={isOpen}
      onClose={onClose}
      onOverlay={onOverlay}
      onEscape={onEscape}
      triggerClosing={triggerClosing}
      onTriggerClosing={onTriggerClosing}
    >
      <BannerModalContent onClose={onTriggerClosing}/>
    </Modal>
  );
});

export { BannerModal };
