import s from './styleProvider';
import React, { memo, useContext, useState } from 'react';
import { MiniSiteContext } from 'templatesReact/miniSiteCommon/Context';
import { CustomButton } from 'components/CustomButton/CustomButton';
import EditIcon from 'images/EditIcon.svg';
import { BannerModal } from 'templatesReact/miniSiteTemplate/bannerModal';

const Banner = memo(() => {
  const { data: {
    isEdit,
    imageUrl,
  } } = useContext(MiniSiteContext);

  const [isOpen, setIsOpen] = useState(false);

  return (
    <div className={s.body}>
      <img src={imageUrl} className={s.image} alt='banner'/>
      {isEdit && <div className={s.editContainer}>
        <CustomButton
          text='Replace image'
          variant='action'
          size='m'
          image={EditIcon}
          imagePos='start'
          isEdit
          onClick={() => setIsOpen(true)}
          width='auto'
        />
      </div>}
      <BannerModal
        isOpen={isOpen}
        onClose={() => setIsOpen(false)}
        onOverlay={() => setIsOpen(false)}
        onEscape={() => setIsOpen(false)}
      />
    </div>
  );
});

export { Banner };
